body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
}

html, body {
    font-size: 18px;
    background-color: #f5f6f7;
}

h1 {
    max-width: 940px;
    font-size: 2.5em;
    font-family: Lato, sans-serif;
    font-weight: 700;
    line-height: 1.1;
    margin: 40px 0;
}


.app {
    min-width: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
}


.contenedor-principal {
    min-height: 600px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
}


@media screen and (max-width: 768px) {

    .contenedor-principal {
        display: flex;
        flex-direction: column;
    }

    h1 {
        font-size: 2em;
    }
}





@media screen and (max-width: 480px) {

    .contenedor-principal {
        display: flex;
        flex-direction: column;
    }

    h1 {
        font-size: 1em;
    }
}


.testimonio {
    background-color: white;
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: 0 3px 13px 1px rgba(0, 0, 0, 0.2);
}


.testimonio__imagen {
    width: auto;
    height: 100%;
}

.testimonio__texto {
    text-align: center;
    padding: 40px;
    font-family: Lato, sans-serif;;
    font-weight: 400;
    line-height: 1.5rem;
}



.testimonio__texto__nombre {
    font-size: 1.3rem;
}



.testimonio__texto__cargo {
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}



.testimonio__texto__texto {
    font-size: 1.1rem;
    text-align: justify;
}


@media screen and (max-width: 768px) {
    .testimonio {
        width: 80%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .testimonio__imagen {
        width: 100%;
        height: auto;
    }

    .testimonio__texto {
        padding: 20px;
    }

    .testimonio__texto__nombre {
        font-size: 1.3rem;
    }

    .testimonio__texto__cargo {
        font-size: 1.3rem;
        margin-top: 15px;
        padding-bottom: 30px;
    }

    .testimonio__texto__texto {
        font-size: 1.1rem;
        text-align: justify;
    }
}


@media screen and (max-width: 480px) {
    .testimonio {
        width: 60%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .testimonio__imagen {
        width: 100%;
        height: auto;
    }

    .testimonio__texto {
        padding: 10px;
    }

    .testimonio__texto__nombre {
        font-size: 1.1rem;
    }

    .testimonio__texto__cargo {
        font-size: 1.1rem;
        margin-top: 7px;
        padding-bottom: 17px;
    }

    .testimonio__texto__texto {
        font-size: 1.1rem;
        text-align: justify;
    }
}


